<template>
	<div class="security">
		<van-nav-bar title="个人资料" fixed left-text="返回" @click-left="goMy" left-arrow />
		<Mymessage>
			<ul>
				<li @click="changePass">
					<div class="left">修改密码</div>
					<div class="right">
						<van-icon name="arrow" />
					</div>
				</li>
				<li @click="CancelNum">
					<div class="left">注销账号</div>
					<div class="right">
						<van-icon name="arrow" />
					</div>
				</li>
			</ul>
		</Mymessage>
		<!-- 修改密码 -->
		<van-popup round v-model="isShowPass" closeable close-icon="close" position="bottom" :style="{ height: '45%' }">
			<h2 style="text-align: center;">注 册</h2>
			<div class="login-textBox" style="padding: 10px 15px;">
				<van-cell-group>
					<van-field v-model="changePassWord.oldPassWord" :type="isText ? 'text':'password' " 
					label="旧密码 :" placeholder="请输入旧密码"
					:right-icon="isText?'eye-o':'closed-eye'" @click-right-icon="toggleType" />

					<van-field v-model="changePassWord.newPassWord" :type="isText ? 'text':'password' " label="新密码 :" placeholder="请输入新密码"
					:right-icon="isText?'eye-o':'closed-eye'" @click-right-icon="toggleType" />
				</van-cell-group>
			</div>
			<div class="btns">
				<van-button class="register" color="#21b276" block round @click="verification">提交</van-button>
			</div>
		</van-popup>
		<div class="out">
			<van-button type="primary" @click="logOut" block color="#21b276" round>退出登录</van-button>
		</div>
	</div>
</template>

<script>
	import "../assets/less/Security.less"
	import { Dialog } from 'vant';
	import {
		utils
	} from "../assets/utils/utils.js"
	import Mymessage from "../components/Mymessage.vue"
	export default {
		name: "Security",
		data() {
			return {
				isShowPass: false,
				//切换密码类型
				isText: false,
				tokenString: "",
				changePassWord: {
					oldPassWord: "",
					newPassWord: "",
				}
			}
		},
		components: {
			Mymessage
		},
		methods: {
			// 密码明文切换
			toggleType() {
				this.isText = !this.isText

			},
			//修改密码弹出窗
			changePass() {
				this.isShowPass = !this.isShowPass
			},
			//提交修改密码
			verification() {
				let obj = {

					password: {
						value: this.changePassWord.newPassWord,
						reg: /^[A-Za-z]\w{5,15}$/,
						errorMsg: '密码支持字母数字_组合且首字符必须为字母'
					},

				};
				if (!utils.validForm(obj)) {
					return;
				}

				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});

				//请求方式
				this.axios({
					method: 'POST',
					url: '/updatePassword',
					//如果是post请求,参数需要挂载到data
					data: {
						tokenString: this.tokenString,
						password: this.changePassWord.newPassWord,
						oldPassword: this.changePassWord.oldPassWord,
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					// console.log(res);
					if (res.data.code == 'E001') {
						if (res.data.result[0] === 1) {
							this.$toast({
								message: res.data.msg,
								forbidClick: true,
								duration: 500
							})
							//如果请求成功关闭注册弹出层
							this.isShowPass = false
							//清除用户注册信息
							for (let key in this.userRegisterInfo) {
								this.userRegisterInfo[key] = ''
							}
							this.$router.push({
								name: "Login"
							})
						}

					}


				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//注销当前账户
			CancelNum() {
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				Dialog.confirm({
					title: '注销账号',
					message: '你真的要确定注销吗?',
				}).then(()=>{
					this.axios({
						method: 'POST',
						url: '/destroyAccount',
						params: {
							tokenString: this.tokenString
						}
						//如果是post请求,参数需要挂载到data
					}).then(res => {
						//如果请求成功清掉提示
						// console.log(res);
						if (res.data.code == 700) {
							return
						}
						if (res.data.code === 'G001') {
							if (res.data.result[0] === 1) {
								this.$toast.loading({
									message: res.data.msg,
									forbidClick: true,
									dutation: 500
								});
							this.$cookies.remove("tokenString");
							this.$router.push({name:"Login"})
							}
						}
					}).catch(eer => {
						console.log("eer==>", eer);
					})
				}).catch(()=>{
					return
				})
				
			},

			//返回上一级
			goMy() {
				this.$router.go(-1)
			},
			//退出登录
			logOut() {
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'POST',
					url: '/logout',
					params: {
						tokenString: this.tokenString
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 700) {
						return

					}
					if (res.data.code === 'F001') {
						if (res.data.result[0] === 1) {
							this.$cookies.remove("tokenString");
							return this.$router.push({
								name: "Login"
							})
						}
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			}
		},

	}
</script>
